import { AnalyticsEventCategory, AnalyticsEventAction } from '../models/analytics'
import ReactGA from 'react-ga'

export default function submitAnalyticsEvent(
	category: AnalyticsEventCategory,
	action: AnalyticsEventAction,
	value?: number | string,
) {
	ReactGA.event({
		category,
		action,
		value: typeof value === 'number' ? value : undefined,
		label: typeof value === 'string' ? value : undefined,
	})
}



