import React from 'react'
import { INotification, NotificationSeverity } from '../../reducers/notifications'
import styles from './styles.sass'
import cn from 'classnames'
import { INotifications } from '../../actions/notifications'

const NOTIFICATION_LIFESPAN = 7000

export interface INotificationsProps {
	notifications: INotifications
}

export interface INotificationsActionProps {
	dismissNotification: (notification: INotification) => void
}

interface IProps extends INotificationsProps, INotificationsActionProps {}

export default class Notifications extends React.Component<
	IProps
> {
	private mounted: boolean = false

	componentDidMount() {
		this.mounted = true
	}

	componentWillUnmount() {
		this.mounted = false
	}

	componentWillReceiveProps(nextProps: IProps) {
		Object.values(nextProps.notifications).forEach((notification) => {
			if (!this.props.notifications[notification.id]) {
				setTimeout(() => {
					if (this.mounted) {
						this.props.dismissNotification(notification)
					}
				}, NOTIFICATION_LIFESPAN)
			}
		})
	}

	render() {
		const { notifications, dismissNotification } = this.props

		return (
			<div className={styles.wrapper}>
				{Object.values(notifications).map((item) => (
					<div key={item.id} className={cn(
						styles.item,
						item.severity === NotificationSeverity.Info && styles.viewInfo,
						item.severity === NotificationSeverity.Warning && styles.viewWarning,
						item.severity === NotificationSeverity.Danger && styles.viewDanger,
					)}>
						<div className={styles.content} onClick={() => dismissNotification(item)}>
							{item.title}
						</div>
						<button
							role="button"
							onClick={() => dismissNotification(item)}
							className={styles.dismiss}
							aria-label="Dismiss notification"
						>
							<i className="fas fa-times"></i>
						</button>
					</div>
				))}
			</div>
		)
	}
}



